import { BrowserRouter as Router, Routes, Route, createBrowserRouter, RouterProvider } from 'react-router-dom';
import { HelmetProvider, Helmet } from 'react-helmet-async';
import Header from './components/Header';
import Footer from './components/Footer';
import Home from './pages/Home';
import Signup from './pages/Signup';
import Login from './pages/Login';
import Contact from './pages/Contact';
import Java from './pages/Java';
import Python from './pages/Python';
import Cicd from './pages/Cicd';
import Automation from './pages/Automation';
import './styles.css';
import React from 'react';

const router = createBrowserRouter(
  [
    { path: '/', element: <><Helmet><title>Codefleet - Home</title></Helmet><Home /></> },
    { path: '/signup', element: <><Helmet><title>Codefleet - Sign Up</title></Helmet><Signup /></> },
    { path: '/login', element: <><Helmet><title>Codefleet - Login</title></Helmet><Login /></> },
    { path: '/contact', element: <><Helmet><title>Codefleet - Contact</title></Helmet><Contact /></> },
    { path: '/java', element: <><Helmet><title>Codefleet - Java</title></Helmet><Java /></> },
    { path: '/python', element: <><Helmet><title>Codefleet - Python</title></Helmet><Python /></> },
    { path: '/cicd', element: <><Helmet><title>Codefleet - CI/CD</title></Helmet><Cicd /></> },
    { path: '/automation', element: <><Helmet><title>Codefleet - Automation</title></Helmet><Automation /></> },
  ],
  { future: { v7_startTransition: true, v7_relativeSplatPath: true } }
);

function App() {
  return (
    <HelmetProvider>
      <div className="flex flex-col min-h-screen">
        <Header className="navbar" />
        <main className="flex-grow pt-16 container mx-auto">
          <RouterProvider router={router} />
        </main>
        <Footer className="footer" />
      </div>
    </HelmetProvider>
  );
}

export default App;