import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isAuthDropdownOpen, setIsAuthDropdownOpen] = useState(false);
  const logo = `${process.env.REACT_APP_API_URL}/static/images/logo.png`;

  return (
    <header className="navbar" id="header">
      <div className="container mx-auto flex flex-col sm:flex-row justify-between items-center">
        <div className="flex items-center justify-between w-full sm:w-auto">
          <div className="flex items-center">
            <img src={logo} alt="Codefleet Logo" className="h-26" id="logo" loading="lazy" />
            <h1 className="text-2xl font-bold ml-2" id="company_name">Codefleet</h1>
          </div>
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="sm:hidden focus:outline-none"
            id="menu_btn"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M4 6h16M4 12h16m-7 6h7"
              />
            </svg>
          </button>
        </div>
        <nav className={`sm:flex ${isMenuOpen ? 'block' : 'hidden'} w-full sm:w-auto mt-4 sm:mt-0`}>
          <div className="flex flex-col sm:flex-row items-center space-y-2 sm:space-y-0 sm:space-x-4">
            <NavLink
              to="/"
              className={({ isActive }) =>
                isActive ? 'text-white' : 'hover:text-white'
              }
              id="menu_home"
            >
              Home
            </NavLink>
            <NavLink
              to="/java"
              className={({ isActive }) =>
                isActive ? 'text-white' : 'hover:text-white'
              }
              id="menu_java"
            >
              Java
            </NavLink>
            <NavLink
              to="/python"
              className={({ isActive }) =>
                isActive ? 'text-white' : 'hover:text-white'
              }
              id="menu_python"
            >
              Python
            </NavLink>
            <NavLink
              to="/cicd"
              className={({ isActive }) =>
                isActive ? 'text-white' : 'hover:text-white'
              }
              id="menu_cicd"
            >
              CI/CD
            </NavLink>
            <NavLink
              to="/automation"
              className={({ isActive }) =>
                isActive ? 'text-white' : 'hover:text-white'
              }
              id="menu_automation"
            >
              Automation
            </NavLink>
            <NavLink
              to="/contact"
              className={({ isActive }) =>
                isActive ? 'text-white' : 'hover:text-white'
              }
              id="menu_contact"
            >
              Contact
            </NavLink>
            <div className="relative">
              <button
                onClick={() => setIsAuthDropdownOpen(!isAuthDropdownOpen)}
                className="btn"
                id="auth_dropdown_btn"
              >
                Account
              </button>
              {isAuthDropdownOpen && (
                <div className="absolute right-0 mt-2 w-48 bg-glass-bg text-primary-blue rounded shadow-lg sm:block" id="auth_dropdown">
                  <NavLink
                    to="/login"
                    className="block px-4 py-2 hover:bg-light-blue"
                    id="menu_login"
                    onClick={() => setIsAuthDropdownOpen(false)}
                  >
                    Sign In
                  </NavLink>
                  <NavLink
                    to="/signup"
                    className="block px-4 py-2 hover:bg-light-blue"
                    id="menu_signup"
                    onClick={() => setIsAuthDropdownOpen(false)}
                  >
                    Sign Up
                  </NavLink>
                </div>
              )}
            </div>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;